import { action, makeObservable, observable, runInAction } from "mobx";

class PageDataStore {
  services = [];
  mainData = [];

  isLoadingJsonData = false;

  constructor() {
    makeObservable(this, {
      services: observable.ref,
      mainData: observable.ref,
      isLoadingJsonData: observable.ref,

      getServices: action,
      getMainData: action,
    });
  }

  getServices = () => {
    runInAction(() => {
      this.isLoadingJsonData = true;
    });
    fetch("/config/pages_data.json")
      .then((res) => {
        return res.json();
      })
      .then(({ services }) => {
        runInAction(() => {
          this.services = services;
          this.isLoadingJsonData = false;
        });
      })
      .catch((e) => {
        console.log(e);
        runInAction(() => {
          this.isLoadingJsonData = false;
        });
      });
  };

  getMainData = () => {
    runInAction(() => {
      this.isLoadingJsonData = true;
    });
    fetch("/config/pages_data.json")
      .then((res) => {
        return res.json();
      })
      .then(({ home }) => {
        runInAction(() => {
          this.mainData = home;
          this.isLoadingJsonData = false;
        });
      })
      .catch((e) => {
        console.log(e);
        runInAction(() => {
          this.isLoadingJsonData = false;
        });
      });
  };
}

export default new PageDataStore();
