import { useEffect } from "react";
import pageDataStore from "stores/pageDataStore";
import classes from "./services.module.scss";
import { observer } from "mobx-react-lite";
import Loading from "components/Loading";

const ServicesPage = () => {
  const { getServices, services, isLoadingJsonData } = pageDataStore;

  useEffect(() => {
    getServices();
  }, []);

  return (
    <div className={classes.servicesWrapper}>
      <div className="container rowGap-30">
        <div className={`${classes.headerContent} fitContent`}>
          <h4 className="title">Our Services</h4>
        </div>
        <div className={`${classes.servicesBlock} rowGap-60`}>
          {services.map((service, i) => {
            const leftItems = service.order_list.slice(0, 2);
            const rightItems = service.order_list.slice(2, 4);
            return (
              <div key={i} className={`${classes.service} rowGap-70`}>
                <div className="text-center rowGap-20">
                  <h5 className="paragraph-black size-25 fw-normal">
                    {service.title}
                  </h5>
                  <p className="paragraph-black size-20">
                    {service.main_description}
                  </p>
                </div>
                <div className={classes.serviceBody}>
                  <div
                    className={`${classes.column} ${classes.left} rowGap-20`}
                  >
                    {leftItems.map((item, index) => (
                      <div key={index} className="rowGap-10">
                        <h5 className="size-26">{item.title}</h5>
                        <p className="paragraph-black size-18">
                          {item.description}
                        </p>
                      </div>
                    ))}
                  </div>
                  <div
                    className={`${classes.column} ${classes.right} rowGap-20`}
                  >
                    {rightItems.map((item, index) => (
                      <div key={index} className="rowGap-10">
                        <h5 className="size-28">{item.title}</h5>
                        <p className="paragraph-black size-20">
                          {item.description}
                        </p>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default observer(ServicesPage);
